import React from 'react';
import{Row,Col} from 'react-bootstrap'
import './style.css'

const Contact = () => {
    return (
        <div style={{backgroundColor: "#2A2A2A"}} id='contact' >
            <div className='container py-5' >
                <Row >
                    <Col lg={5} md={12}>
                        <div className='info-contact text-start'>
                            <div className='title mb-5'>
                                <h3>My Contacts</h3>
                            </div>
                            <p>Contrary to popular belief, Lorem Ipsum is not simply random text.
                                 It has roots in a piece of classical Latin literature from 45 BC,
                                  making it over 2000 years old.
                            </p>
                            <div className='address mt-4'>
                                <h4>Address</h4>
                                <p>245 King Street, Touterie Victoria 8520 Australia</p>
                            </div>
                            <div className='phone mt-4'>
                                <h4>Phone</h4>
                                <p className='m-0'>0-123-456-7890</p>
                                <p>0-123-456-7890</p>
                            </div>
                            <div className='email mt-4' >
                                <h4>Email</h4>
                                <p>sample@gmail.com</p>
                            </div>
                        </div>
                    </Col>
                    <Col lg={7} md={12}>
                        <form className='contact-form'>
                            <div className='title mb-4 text-start'>
                                <h3 className='m-0' >My Contacts</h3>
                            </div>
                            <Row >
                                <Col lg={6} md={6}>
                                        <input type="text " class=" input-name" placeholder='First Name'/>
                                </Col>
                                <Col lg={6} md={6}>
                                        <input type="text " class=" input-name" placeholder='Last Name'/>
                                </Col>
                                <Col lg={6} md={6}>
                                        <input type="email " class=" input-name" placeholder='Email'/>
                                </Col>
                                <Col lg={6} md={6}>
                                        <input type="text " class=" input-name" placeholder='Subject'/>
                                </Col>
                                <Col lg={12} >
                                    <textarea style={{height:"150px" }} type="text " class=" input-name" placeholder='messag'></textarea>
                                </Col>
                                <div className='text-start mt-3 '>
                                    <button className='submit-form px-3 py-2'>
                                    Send Message
                                    </button>
                                </div>
                            </Row>
                        </form>
                    </Col>
                </Row>
            </div>
        </div>
    );
};

export default Contact;