import React,{useState} from 'react';
import Link from 'react-router-dom'
import {GrClose} from 'react-icons/gr'

import './style.css'

function SideBar({sidebar,closeSidebar})  {
    
    return (
        <div className={sidebar?"sidebar sidebar--open":"sidebar"}>
                <div className='close-bar'>
                    <span onClick={closeSidebar}><i class="fa-solid fa-xmark"></i></span>
                </div>
                <div className='listbar'>
                    <div className='itembat'>
                        <div className='homebar' style={    {position: "relative"}}>
                            <img className='w-100' src={require('../assets/images/home.png')} />
                            <div className='overlay'> <a href='#carusal'>HOME</a> </div>
                        </div>
                    </div>
                    <div className='itembat'>
                        <div className='homebar' style={    {position: "relative"}}>
                            <img className='w-100' src={require('../assets/images/about.jpg')} />
                            <div className='overlay'> <a href='#about'>ABOUT ME</a> </div>
                        </div>
                    </div>
                    <div className='itembat'>
                        <div className='homebar' style={    {position: "relative"}}>
                            <img className='w-100' src={require('../assets/images/service.png')} />
                            <div className='overlay'> <a href='#service'>MY SERVICE</a> </div>
                        </div>
                    </div>
                    <div className='itembat'>
                        <div className='homebar' style={    {position: "relative"}}>
                            <img className='w-100' src={require('../assets/images/porfolio.jpg')} />
                            <div className='overlay'> <a href='#portfolio'>PORTFOLIO</a> </div>
                        </div>
                    </div>
                    <div className='itembat'>
                        <div className='homebar' style={    {position: "relative"}}>
                            <img className='w-100' src={require('../assets/images/blog.jpg')} />
                            <div className='overlay'> <a href='#blog'>BLOG</a> </div>
                        </div>
                    </div>
                    <div className='itembat'>
                        <div className='homebar' style={    {position: "relative"}}>
                            <img className='w-100' src={require('../assets/images/contact.jpg')} />
                            <div className='overlay'> <a href='#contact'>CONTACT</a> </div>
                        </div>
                    </div>
                </div>

        </div>
    );
};

export default SideBar;