import React from 'react';
import './style.css'

const Carusal = () => {
    return (
        <div className='carousal' id='carusal' >
            <div className='carousal-content'>
                <div className='carousal-image     position-relative'>
                    <img style={{width:"100%" ,   minHeight: "700px" }} src={require('../assets/images/carusal.jpg')} />
                </div>
                <div className='content container'>
                    <div className='d-flex'>
                        <p className='m-0'>I AM Mohamed Nooh</p>
                        <span>   </span>
                    </div>
                    <h2> front end developer </h2>
                    <div className='contact'>
                        <a href='' > contact me </a>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Carusal;